import React, { useState } from "react";
import { Link } from "gatsby";
import "./projects.css";
import SEO from "../components/seo"
import Layout from "../components/layout"
import Modal from "../components/modal";
import { civil_data, mining_data } from "../data/projectData";


const TextBox = (props) => {
  const data = props.data;
  const [clickedIndex, setClickedIndex] = useState();

  const sendIndex = (imageIndex) => {
    let maxNumber = data[imageIndex].image_array.length - 1;

    var modal = <Modal 
    startIndex = { 0 }
    imageItems = { data[imageIndex].image_array }
    onClose = { () => setClickedIndex(null) }
    maxNumber = { maxNumber }
    />;
    setClickedIndex(modal);
  }

  return(
    <ul>
      {data.map((e,i) => 
      <li key={i}
        onClick={() => sendIndex(i)}>
        <div className="text_box">
          <span>
            <i className="fas fa-users blue "></i><br/>
            <i className="far fa-clipboard blue"></i>
          </span>
          <span>
            <h3>{e.client}</h3>
            <h3>{e.title}</h3>
          </span>
          <span>
            <img 
              src={e.image_array[0].image} 
              alt="project sample" 
              width="80" 
              height ="80"
            />
          </span>
        </div>
      </li>
      )}
      {clickedIndex}
    </ul>
  )
}


export default function Project() {
  return (
    <>
      <Layout useAccentColorForNav={true}>
      <SEO title="Home" />

      <div id="project_container">
        <section className="category_list">
          <ul>
            <li><Link to="#civil">civil/ architecture</Link></li>
            <li><Link to="#minig">minig/ heavy industory</Link></li>
            <li><Link to="/">automotive</Link></li>
            <li><Link to="/">water</Link></li>
            <li><Link to="/">oil/ gas</Link></li>
            <li><Link to="/">power</Link></li>
            <li><Link to="/">robotics/ automation</Link></li>
          </ul>
        </section>
        
        <section className="project_list">
          <div id="list_wrapper">
            <h1>Projects</h1>
              <div className="list_box">
                <h2 id="civil">civil/architecture</h2>
                <TextBox data={civil_data} />
              </div>
              <div className="border"></div>
              <div className="list_box">
                <h2 id="minig">minig/ heavy industory</h2>
                <TextBox data={mining_data} />
              </div>
              <div className="border"></div>
          </div>
        </section>
      </div>
      </Layout>
    </>
  )
}